@use "base/variables" as v;
@use "base/mixins" as m;

.form {
  margin-top: 5rem;
  width: 100%;
}

.field {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;

  label {
    flex: 0 0 10rem;
  }

  input {
    flex: 1;
    border: none;
    padding: calc(v.$separation / 4);
    border-radius: 1rem;

    &:disabled {
      background-color: lighten(v.$black, 30%);
      color: v.$white;
      cursor: not-allowed;
    }
  }
}
