@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  @include m.tablet {
    height: 100%;
  }
  display: flex;
  flex-direction: column;
  background-color: v.$black;
  color: v.$white;
  font-family: v.$mainFont;
  font-size: 1.6rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}
