@use "base/variables" as v;
@use "base/mixins" as m;

.appointments {
  list-style: none;
  padding: 0;

  li {
    padding: 1rem;
    border-top: 1px solid v.$white;
  }

  h3 {
    text-align: left;
  }

  p {
    font-weight: v.$bold;
    color: v.$blue;
  }

  span,
  .service {
    font-weight: v.$regular;
    color: v.$white;
  }
}

.services {
  @extend .appointments;

  .actions {
    margin: 0;
  }

  .button,
  .delete-button {
    margin: 0;
  }
}
