@use "base/variables" as v;
@use "base/mixins" as m;

.content-app {
  @include m.tablet {
    display: grid;
    grid-template-columns: 1fr 2fr;
    min-height: 100vh;
  }

  .image {
    height: 20rem;
    background-image: url(../img/1.jpg);
    background-size: cover;
    background-position: center center;

    @include m.tablet {
      height: auto;
    }
  }

  .app {
    width: 95%;
    margin: 0 auto;
    padding: 3rem 0;
    @include m.tablet {
      overflow: scroll;
    }
  }
}

.bar {
  margin: v.$separation 0;

  p {
    text-align: center;
    @include m.tablet {
      text-align: left;
    }
  }

  @include m.tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .button {
    @include m.button(v.$black, v.$white);
    margin: 0;
  }
}

.service-bar {
  margin-bottom: v.$separation;
  @include m.desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .button {
    margin: 0 0 1rem 0;
  }
}
